import React from "react";
import Button from "@mui/material/Button";

const OrderCancelButton = ({ cancelOrder, orderNumber }) => {
  return (
    <Button
      variant="contained"
      color="error"
      onClick={() => {
        if (window.confirm("  ?")) {
          cancelOrder(orderNumber);
        }
      }}
      style={{ whiteSpace: "nowrap" }}
    >
       
    </Button>
  );
};

export default OrderCancelButton;
