import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  Container,
  TextField,
  Button,
  Typography,
  Grid,
  Dialog,
  DialogTitle,
  DialogContent,
} from "@mui/material";
import DaumPostcode from "react-daum-postcode";
import { getUserInfo, updateUserInfo, deleteUserInfo } from "../../lib/api";

const Mypage = () => {
  const navigate = useNavigate();

  const [user, setUser] = useState({
    userName: "",
    email: "",
    age: "",
    phone: "",
    address: "",
  });
  const shortId = localStorage.getItem("shortId");
  const accessToken = localStorage.getItem("accessToken");

  //   
  // useEffect(() => {
  //   getUserInfo(shortId, accessToken)
  //     .then((data) => {
  //       setUser(data.user); //  data response.data 
  //     })
  //     .catch((error) => {
  //       console.error("    :", error);
  //     });
  // }, []);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await getUserInfo();
        if (response.status === 200) {
          setUser(response.data.user);
        }
      } catch (e) {
        throw new Error("");
      }
    };
    fetchData();
  }, []);

  //   
  const handleUpdate = (updatedInfo) => {
    updateUserInfo(shortId, accessToken, updatedInfo)
      .then((data) => {
        //    
        setUser(data.user);
        console.log("  .");
      })
      .catch((error) => {
        console.error("   :", error);
      });
  };
  //   
  const handleDelete = () => {
    deleteUserInfo(shortId, accessToken)
      .then(() => {
        //    
        console.log(" .");
        //          .
        navigate("/");
      })
      .catch((error) => {
        console.error("  :", error);
      });
  };

  const daum = window.daum;

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUser((prevUser) => ({
      ...prevUser,
      [name]: value,
    }));
  };

  const [isPostcodeOpen, setPostcodeOpen] = useState(false);
  const [postcode, setPostcode] = useState("");
  const [address, setAddress] = useState("");
  const [isUsingAccountAddress, setUsingAccountAddress] = useState(true);

  const handleComplete = (data) => {
    setPostcode(data.zonecode);
    setAddress(data.address);
    setPostcodeOpen(false);
    setUser({ ...user, address: data.address });
  };

  const handleOpenPostcode = () => {
    new daum.Postcode({
      oncomplete: function (data) {
        const fullAddress = data.address;
        setUser({ ...user, address: fullAddress });
      },
    }).open();
  };

  const useAccountAddress = () => {
    const accountAddressValue = "Account Address Here";

    setUsingAccountAddress(true);
    setAddress(accountAddressValue);
    setUser({ ...user, address: accountAddressValue });
  };

  return (
    <div
      style={{
        display: "flex",
        marginTop: "80px",
        alignItems: "center",
        height: "100vh",
      }}
    >
      <Container maxWidth="sm">
        <Typography variant="h5" gutterBottom style={{ fontWeight: "bold" }}>
            
        </Typography>
        <form>
          <Typography variant="h6" style={{ fontWeight: "bold" }}>
            
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            margin="normal"
            value={user.userName}
            onChange={handleInputChange}
            name="userName"
          />
          <Typography variant="h6" style={{ fontWeight: "bold" }}>
            
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            margin="normal"
            value={user.email}
            onChange={handleInputChange}
            name="email"
          />
          <Typography variant="h6" style={{ fontWeight: "bold" }}>
            
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            margin="normal"
            value={user.age ? user.age : ""}
            onChange={handleInputChange}
            name="age"
          />
          <Typography variant="h6" style={{ fontWeight: "bold" }}>
            
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            margin="normal"
            value={user.phone ? user.phone : ""}
            onChange={handleInputChange}
            name="phone"
          />
          <Typography variant="h6" style={{ fontWeight: "bold" }}>
             
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            margin="normal"
            value={
              isUsingAccountAddress
                ? user.address
                  ? user.address
                  : ""
                : (postcode || "") + " " + (address || "")
            }
            onClick={() => setPostcodeOpen(true)}
          />
          <Dialog
            open={isPostcodeOpen}
            onClose={() => setPostcodeOpen(false)}
            sx={{ "& .MuiDialog-paper": { width: "80%", height: "80%" } }}
          >
            <DialogTitle>  </DialogTitle>
            <DialogContent>
              <DaumPostcode onComplete={handleComplete} />
            </DialogContent>
          </Dialog>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Button
                variant="contained"
                color="primary"
                fullWidth
                onClick={handleUpdate}
              >
                  
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="contained"
                color="error"
                fullWidth
                onClick={handleDelete}
              >
                
              </Button>
            </Grid>
          </Grid>
        </form>
      </Container>
    </div>
  );
};

export default Mypage;
